<template>
	<scroll class="suggest" :data="result" :pullup="pullup" @scrollToEnd="searchMore" ref="suggest" :beforeScroll="beforeScroll" @beforeScroll="listScroll">
		<ul class="suggest-list">
			<li @click="selectItem(item)" class="suggest-item" v-for="item in result" :key="item.id">
				<div class="icon">
					<i :class="getIconCls(item)"></i>
				</div>
				<div class="name">
					<p class="text" v-html="getDisplayName(item)"></p>
				</div>
			</li>
			<loading v-show="hasMore" title=""></loading>
		</ul>
		<div class="no-result-wrapper" v-show="!hasMore && !result.length">
			<no-result title=","></no-result>
		</div>
	</scroll>
</template>

<script type="text/ecmascript-6">
	import {search} from 'api/search'
	import {ERR_OK} from 'api/config'
	// import {createSong, jqueryUrl, getSongmp3} from 'common/js/song'
	import {createSong} from 'common/js/song'
	import Scroll from 'base/scroll/scroll'
	import Loading from 'base/loading/loading'
	import Singer from 'common/js/singer'
	import {mapMutations, mapActions} from 'vuex'
	import NoResult from 'base/no-result/no-result'
	const TYPE_SINGER = 'singer'
	const perpage = 30
	export default {
		data() {
			return {
				page: 1,
				result: [],
				pullup: true,
				hasMore: true,
				deltaTotal: 0,
				beforeScroll: true
			}
		},
		props: {
			query: {
				type: String,
				default: ''
			},
			showSinger: {
				type: Boolean,
				default: true
			}
		},
		methods: {
			refresh() {
				this.$refs.suggest.refresh()
			},
			selectItem(item) {
				if (item.type === TYPE_SINGER) {
					const singer = new Singer({
						id: item.singermid,
						name: item.singername
					})
					this.$router.push({
						path: `/search/${singer.id}`
					})
					this.setSinger(singer)
				} else {
					// console.log(item)
					this.insertSong(item)
				}
				this.$emit('select')
			},
			getIconCls(item) {
				if (item.type === TYPE_SINGER) {
					return 'icon-mine'
				} else {
					return 'icon-music'
				}
			},
			getDisplayName(item) {
				if (item.type === TYPE_SINGER) {
					return item.singername
				} else {
					return `${item.name}-${item.singer}`
				}
			},
			search() {
				// this.result = ''
				this.hasMore = true
				this.page = 1
				this.$refs.suggest.scrollTo(0, 0)
				search(this.query, this.page, this.showSinger, perpage).then((res) => {
					if (res.code === ERR_OK) {
						// console.log(res.data)
						this._genResult(res.data)
						this._checkMore(res.data)
					}
				})
			},
			searchMore () {
				if (!this.hasMore) {
					return
				}
				this.page++
				search(this.query, this.page, this.showSinger, perpage).then((res) => {
					if (res.code === ERR_OK) {
						// console.log(res.data)
						this._genResult(res.data)
						this._checkMore(res.data)
					}
				})
			},
			listScroll() {
				this.$emit('listScroll')
			},
			_checkMore(data) {
				const song = data.song
				if (!song.list.length || (song.curnum + song.curpage * perpage) >= song.totalnum - this.deltaTotal) {
					this.hasMore = false
				}
			},
			_genResult(data) {
				// console.log(data)
				let ret = []
				if (data.zhida && data.zhida.singerid) {
					ret.push({...data.zhida, ...{type: TYPE_SINGER}})
				}
				if (data.song) {
					// console.log(1)
					// console.log(ret)
					let temp = this._normalizeSongs(data.song.list)
					// console.log(1)
					setTimeout(() => {
						let tempResult = this.result
						if (this.page === 1) {
							this.result = ret.concat(temp)
						} else {
							this.result = tempResult.concat(temp)
						}
						// if (tempResult[0].type === TYPE_SINGER) {
						// 	temp.shift()
						// }
						// this.result = tempResult.concat(temp)
						// ret.shift()
						// console.log(this.result)
						// return ret
					}, 500)
					// ret = ret.push(this._normalizeSongs(data.song.list))
					// console.log(2)
					// console.log(ret)
					// console.log(2)
				}
				// console.log(9)
				// console.log(ret)
				// console.log(9)
				// return ret
			},
			_normalizeSongs(list) {
				let ret = []
				list.forEach((musicData) => {
					if (musicData.songid && musicData.albummid) {
						// ret.push(createSong(musicData, 'www.baidu.com'))
						// console.log(musicData)
						ret.push(createSong(musicData, ''))
						// getSongmp3(musicData.songmid).then((res) => {
						// 	// let urlObject = this.jqueryUrl(res.data[0])
						// 	let urlObject = jqueryUrl(res.data[0])
						// 	// console.log(urlObject)
						// 	if (urlObject.vkey) {
						// 		ret.push(createSong(musicData, res.data[0]))
						// 	} else {
						// 		this.deltaTotal++
						// 	}
						// })
					}
				})
				// console.log(4)
				// console.log(ret)
				// console.log(4)
				return ret
			},
			...mapMutations({
				setSinger: 'SET_SINGER'
			}),
			...mapActions([
				'insertSong'
			])
		},
		watch: {
			query() {
				this.result = []
				this.page = 1
				this.search()
			}
		},
		components: {
			Scroll,
			Loading,
			NoResult
		}
	}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
	@import "~common/stylus/variable"
	@import "~common/stylus/mixin"

	.suggest
		height: 100%
		overflow: hidden
		.suggest-list
			padding: 0 30px
			.suggest-item
				display: flex
				align-items: center
				padding-bottom: 20px
			.icon
				flex: 0 0 30px
				width: 30px
				[class^="icon-"]
					font-size: 14px
					color: $color-text-d
			.name
				flex: 1
				font-size: $font-size-medium
				color: $color-text-d
				overflow: hidden
				.text
					no-wrap()
		.no-result-wrapper
			position: absolute
			width: 100%
			top: 50%
			transform: translateY(-50%)
</style>


